import * as React from 'react';
import { StyleRules, WithStyles, withStyles } from '@material-ui/core/styles';
import { Tabs, Tab, Typography } from '@material-ui/core';
import Terms from './Terms';
import Policy from './Policy';
import LinkContent from './LinkContent'

const styles: StyleRules<'top'|'contents'|'header'|'desc'> = {
    top: {
        width: '100%',
    },
    header: {
        marginLeft: 10,
        display: 'flex',
        justifyContent: 'center',
        width: '100%',
        color: '#C0C0C0'
    },
    contents: {
        display: 'flex',
        justifyContent: 'center',
        flexDirection: 'column',
        alignItems: 'center',
        paddingTop: 60,
        paddingBottom: 15
    },
    desc: {
        margin: 15,
        color: '#c0c0c0',
        width: '80%'
    }
};

type ClassNames = keyof typeof styles;

interface MyPrjProps {
    headerIn: boolean;
    rootHeight: number;
    before: () => void;
}

interface ThisState {
    tabIndex: number;
    tabInit: boolean;
}

class MyPrj extends React.Component<MyPrjProps & WithStyles<ClassNames>, 
                                            ThisState> {

    constructor(props: any){
        super(props);
        this.state = {
            tabIndex: 0,
            tabInit: false
        }
    }

   public render(): JSX.Element {
        const { classes, before, rootHeight } = this.props;
        const { tabIndex } = this.state;
        return (
        <div 
           className={classes.top} 
           style={{ height: rootHeight }}
        >
            <LinkContent 
                beforeLabel={"My project"}
                before={before}
            />
            <div className={classes.contents}>
                <Typography variant="body1" style={{color: '#c0c0c0', marginLeft: 15, marginRight: 15}}>
                    aKnow2名義でリリースしているアプリやサービスの利用規約とプライバシーです。
                </Typography>
                <Tabs className={classes.header} value={tabIndex} onChange={this.changeTabIndex}>
                    <Tab label="利用規約" />
                    <Tab label="ポリシー" />
                </Tabs>
                <div>
                    { tabIndex === 0 && <Terms /> }
                    { tabIndex === 1 && <Policy /> }
                </div>
            </div>
        </div>
        );
    }

    private changeTabIndex = (event: any, index: number) => {
        this.setState({tabIndex: index});
    }
}

const StyledContainer = withStyles<{} & ClassNames>(styles)(MyPrj);

export default StyledContainer;
