import * as React from 'react';
import { withStyles, Button, Slide, Dialog, IconButton } from '@material-ui/core';
import Menu from '@material-ui/icons/Menu';
import Cancel from '@material-ui/icons/Cancel';
import { StyleRules, WithStyles } from '@material-ui/core/styles';
import Top from './Top';
import MyPrj from './MyPrj';
import Terms from './TermAndPolicy';
import Flick, { FlickEvent } from './logic/Flick';

const styles: StyleRules<'root'|'menuButton'|'term'|'contact'|'top'|'tab'|'myProject'|'surface'> = {
  root: {
    position: 'relative',
    top: 0,
    bottom:0,
    left:0,
    margin: 0,
    width: '100%',
    perspective: '80vh',
  },
  menuButton: {
    position: 'absolute',
    zIndex: 10,
    right: 0,
    top: 0,
    marginRight: 10,
    marginTop: 10,
  },
  surface: {
    transformOrigin: '50% 50% -90vh',
    position: 'absolute',
    transformStyle: 'preserve-3d',
    left: '0',
    top: '0',
    width: '100%',
  },
  top: {
    color: '#FFFFFF',
    backgroundColor: '#C0C0C0',
  },
  myProject: {
    color: '#FFFFFF',
    backgroundColor: '#2F2F2F',
  },
  contact: {
    backgroundColor: '#2F2F2F',
  },
  term: {
    backgroundColor: '#3e2723',
  },
  tab: {
    color: '#FFFFFF'
  },
};

type ClassNames = keyof typeof styles;

interface Surface {
    x: number;
    style: React.CSSProperties;
}

interface AppState {
  showMenu: boolean;
  rootHeight: number;
  path?: string;
  menuIn: boolean;
  top: Surface;
  myProject: Surface;
  term: Surface;
  contact: Surface;
}
const getZindex = (x: number) => {
  const absX = Math.abs(x)/60;
  switch(absX){
    case 0:
      return 5;
    case 1:
      return 4;
    case 2: 
      return 3;
    default: 
      return 0;
  }
}

export enum InitPos {
  top=0,
  myPrj=60,
  tp=120,
}

export interface AppPorps {
  initPos: InitPos;
}

class App extends React.Component<WithStyles<ClassNames> & AppPorps, AppState> {

  private topElemet: HTMLDivElement;

  constructor(props: WithStyles<ClassNames> & AppPorps) {
    super(props);
    const surface ={
        x: 0,
        style: {}
    } 
    this.state = {
      showMenu: false,
      rootHeight: 0,
      menuIn: true,
      top: surface,
      myProject: surface,
      term: surface,
      contact: surface,
    };
  }

  onChangedWindowSize = () => {
    const state = this.state
    const top = Object.assign({}, state.top)
    const myProject = Object.assign({}, state.myProject)
    const term = Object.assign({}, state.term)
    const contact = Object.assign({}, state.contact)
    const height = window.innerHeight

    const newStyle = ((h: number) => {
      return (style: React.CSSProperties) => {
        return Object.assign(style, { height: h+'px' } as React.CSSProperties)
      }
    })(height)
    
    top.style = newStyle(top.style)
    myProject.style = newStyle(myProject.style)
    term.style = newStyle(term.style)
    contact.style = newStyle(contact.style)

    this.setState({
      rootHeight: height,
      top,
      term,
      myProject,
      contact
    })
  }

  componentDidMount() {
    const props = this.props;
    const initSurfaceGenerator = function* (base: number){
      const deg = -60;
      let ratio = 0;
      while(true) {
        const toDeg = base + deg * ratio;
        yield {
            x: toDeg,
            style: {
              transform: `rotateX(${toDeg}deg)`,
              zIndex: getZindex(toDeg),
              height: window.innerHeight+'px'
            }
        } as Surface;
        ratio++;
      }
    }
    const itr = initSurfaceGenerator(props.initPos);
    this.setState({
      rootHeight: window.innerHeight,
      menuIn: true,
      top: itr.next().value,
      myProject: itr.next().value,
      term: itr.next().value,
      contact: itr.next().value,
    });
    window.addEventListener('resize', this.onChangedWindowSize)

    Flick.addListener((event) => {
      const routeList = ['/', '/prj', '/terms']
      const x = this.state.top.x;
      const getUDRoute = (x: number) => {
        let current = x;
        for (let i = 0; i < routeList.length ; i++) {
          current = x % 60;
          if (current === 0) {
            if (i === 0) {
              return [null, routeList[i+1]]
            } else if (i === (routeList.length-1)) {
              return [routeList[i-1], null]
            }
            return [routeList[i-1], routeList[i+1]]
          }
        }
        return [null, null];
      }
      const [prev, next] = getUDRoute(x);
      console.log(prev + ' ' + next)
      if (event === FlickEvent.up) {
        if (next) {
          this.rotate(-60, next)
        }
      } else if ( event === FlickEvent.down) {
        if (prev) {
          this.rotate(60, prev)
        }
      }
    })
    
  }

  public render() {
    const {classes} = this.props;
    const {top, myProject, term, menuIn, showMenu, rootHeight } = this.state;
    console.log('render', top.style)
    return (
      <div className={classes.root} style={{ height: rootHeight }}>
        <Slide direction="left" in={menuIn}>
          <div className={classes.menuButton}>
            <Button onClick={this.showMenu}>
              <Menu style={{color: '#FFFFFF'}} fontSize={'large'}/>
            </Button>
          </div>
        </Slide>
        <div
          className={classes.surface+' '+classes.top}
          style={top.style}
          ref={this.refTop} >
            <Top next={this.toMyProject} show={menuIn} rootHeight={rootHeight} />
        </div>
        <div className={classes.surface+' '+classes.myProject} style={myProject.style}>
          <MyPrj next={this.toTerms} before={this.toTop} rootHeight={rootHeight} />
        </div>
        <div className={classes.surface+' '+classes.term} style={term.style}>
          <Terms headerIn={menuIn} before={this.toMyProject} rootHeight={rootHeight}/>
        </div>
        <Dialog
          open={showMenu}
          fullScreen={true}
        >
         <div 
          style={{
            width: '100%',
            position: 'relative',
            height: '100%',
            backgroundColor: '#404040',
            display: 'flex',
            flexDirection: 'column',
            justifyContent: 'center',
            alignItems: 'center',
          }}
         >
            <IconButton 
              style={{
                position: 'absolute',
                top: 10,
                right: 20,
              }}
              onClick={this.closeMenu}
            >
              <Cancel fontSize={'large'} style={{color: '#FFFFFF'}}/>
            </IconButton>
              <Button style={{color: '#c0c0c0', fontSize: 48}} onClick={this.toTop}>
                About
              </Button>
              <Button style={{color: '#c0c0c0', fontSize: 48}} onClick={this.toMyProject}>
                My project
              </Button>
              <Button style={{color: '#c0c0c0', fontSize: 48}} onClick={this.toTerms}>
                Terms
              </Button>
         </div>
        </Dialog>
      </div>
    );
  }

  private showMenu = () => {
    this.setState({showMenu: true});
  }

  private closeMenu = () => {
    this.setState({showMenu: false});
  }

  private refTop = (ref: HTMLDivElement|null) => {
    if (ref) {
      this.topElemet = ref;
    }
  }
  private toMyProject = () => {
    this.closeMenu();
    const deltaX = -1 * this.state.myProject.x;
    this.rotate(deltaX, '/prj');
 }
 private toTerms = () => {
    this.closeMenu();
    const deltaX = -1 * this.state.term.x;
    this.rotate(deltaX, '/terms');
 }
 
  private toTop = () => {
    this.closeMenu();
    const deltaX = -1 * this.state.top.x;
    this.rotate(deltaX, '/');
 }

 private rotate = (deltaX: number, route) => {

    if (deltaX === 0) {
      return;
    }

    const scale = 0.6;
    const orgTop = this.state.top; 
    const orgMyPrj = this.state.myProject; 
    const orgTerm = this.state.term; 
    const orgPolicy = this.state.contact; 

    const baseDeg = 60;
    const zoomTransitionTime = 0.3;
    const transitionTime = 0.5*((Math.abs(deltaX))/baseDeg);
    const topDeltaX = orgTop.x + deltaX;
    const myPrjDeltaX = orgMyPrj.x + deltaX;
    const termDeltaX = orgTerm.x + deltaX;
    const policyDeltaX = orgPolicy.x + deltaX;


    const createRotateSurface = (delta: number) => {
      return {
          x: delta,
          style: {
            transform: `scale(${scale}) rotateX(${delta}deg)`,
            transition: `${transitionTime}s`,
            zIndex: getZindex(delta)
          }
        } as Surface;
    };

    const createZoomSurface = (delta: number) => {
      return  {
          x: delta,
          style: {
            transform: `scale(1) rotateX(${delta}deg)`,
            transition: `${zoomTransitionTime}s`,
            zIndex: getZindex(delta)
          }
        } as Surface;
    };
 
    function* animationSequence(){
      yield {
        top: createRotateSurface(topDeltaX),
        myProject: createRotateSurface(myPrjDeltaX),
        term: createRotateSurface(termDeltaX),
        contact: createRotateSurface(policyDeltaX)
      } as AppState;       
      yield {
        top: createZoomSurface(topDeltaX),
        myProject: createZoomSurface(myPrjDeltaX),
        term: createZoomSurface(termDeltaX),
        contact: createZoomSurface(policyDeltaX),
      } as AppState;   
    }
    
    const itr = animationSequence();

    const listener = () => {
      const result = itr.next();

      const newState = result.value;
      if (newState) {
        this.setState(newState);
      }

      if (result.done) {
        this.topElemet.removeEventListener('transitionend', listener);
        this.setState({
          menuIn: true
        });
        window.history.replaceState('', '', route);
        window.history.pushState('', '', route);
      }
    };
    this.topElemet.addEventListener('transitionend', listener);
    this.setState({
      menuIn: false
    });
    const createZoomOutSurface = (org: Surface) => {
      return {
        x: org.x,
        style: {
          transform: `scale(${scale}) rotateX(${org.x}deg)`,
          transition: `${zoomTransitionTime}s`,
          zIndex: getZindex(org.x)
        }
      }
    } 
    this.setState({
      top: createZoomOutSurface(orgTop),
      myProject: createZoomOutSurface(orgMyPrj),
      term: createZoomOutSurface(orgTerm),
      contact:createZoomOutSurface(orgPolicy),
    });
  }
}


export default withStyles<{} & ClassNames>(styles)(App);
