interface Pos {
  x: number;
  y: number;
}

export enum FlickEvent {
  down='down',
  up='up',
  none='none'
}
export type FlickListener  = (event: FlickEvent) => void;

class Flick {

  startpos: Pos = {x: 0, y: 0 };
  endPos: Pos = { x:0, y: 0 };

  listeners: FlickListener[] = []

  addListener(listener: FlickListener) {
    this.listeners.push(listener)
    window.addEventListener('touchstart', (e: TouchEvent) => {
      if (e.targetTouches.length == 1) {
        var touch = e.targetTouches[0];
        this.setStart(touch.screenX, touch.screenY)
      }
    })

    window.addEventListener('touchend', (e) => {
      if (e.changedTouches.length == 1) {
        var touch = e.changedTouches[0];
        this.setEnd(touch.screenX, touch.screenY)
      }
    })
  }

  setStart(x,y) {
    const pos = this.startpos;
    pos.x = x;
    pos.y = y;
  }

  setEnd(x, y) {
    const pos = this.endPos;
    pos.x = x;
    pos.y = y;
    this.judge()
  }

  judge() {
    const s = this.startpos;
    const e = this.endPos;

    const result = (() => {
      if (s.y > e.y) {
        return FlickEvent.down
      } else if(s.y < e.y) {
        return FlickEvent.up
      } else {
        return FlickEvent.none
      }
    })()
    
    this.listeners.forEach((l) => {
      l(result)
    })
  }
}

const flick = new Flick()


export default flick;