import * as React from 'react';
import { StyleRules, WithStyles, withStyles } from '@material-ui/core/styles';
import { Typography, IconButton, Slide, Fade } from '@material-ui/core';
import commonStyles from './styles';
import LinkContent from './LinkContent';
import './Top.css';
const Twitter = require('./icons/twitter.svg');
const Fb = require('./icons/facebook.svg');

const styles: StyleRules<'social'|'root'|'content'|'icon'|'next'> = {
    root: {
        width: '100%',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        backgroundImage: 'linear-gradient(-90deg, #265154, #542631)',
        backgroundSize: '200% 100%',
        animation: 'Background 30s ease infinite'
    },
    content: {
        width: '80%',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
    },
    icon: {
        width: 56,
        height: 56,
    },
    social: {
        position: 'absolute',
        top: 0,
    },
    next: commonStyles.next
};

type ClassNames = keyof typeof styles;

interface TopProps {
    next: () => void;
    show: boolean;
    rootHeight: number
}

class Top extends React.Component<TopProps&WithStyles<ClassNames>, 
                                   {}> {

   public render(): JSX.Element {
        const {classes, next, show, rootHeight} = this.props;
        return (
            <div className={classes.root} style={{height: rootHeight}}>
                <Fade in={show} timeout={{enter: 5000, exit: 100}}>
                    <div style={{ overflow: 'hidden'}}>
                        <div style={{
                            animation: 'Spin 5s linear infinite',
                            opacity: 0.3,
                            position: 'absolute',
                            left: 0,
                            top: '85vh',
                            background: '#ef5350',
                            borderRadius: '50% 50% 50% 70%/50% 50% 70% 60%',
                            width: '100vw',
                            height: '100vw' }}/>
                        <div style={{
                            animation: 'Spin 6s linear infinite',
                            opacity: 0.3,
                            position: 'absolute',
                            left: 0,
                            top: '85vh',
                            background: '#039BE5',
                            borderRadius: '60% 60% 50% 70%/60% 50% 70% 80%',
                            width: '100vw',
                            height: '100vw' }}/>
                        <div style={{
                            animation: 'Spin 5s linear infinite',
                            opacity: 0.3,
                            position: 'absolute',
                            left: 0,
                            top: '85vh',
                            background: '#ce93d8',
                            borderRadius: '70% 50% 60% 70%/60% 70% 50% 60%',
                            width: '100vw',
                            height: '100vw' }}/>               
                    </div>
                </Fade>
                <LinkContent 
                    next={next}
                    nextLabel={'My project'}
                />
                <div className={classes.content}>
                <Typography color='inherit' variant="title" style={{color: '#C0C0C0', zIndex: 30}}>
                    こんにちは。<br />
                    三重県津市で働くソフトウェア開発者、金山です。<br />
                    得意&#x1f44d;フロントエンド、モバイル・デスクトップアプリ、クロスプロットフォーム<br />
                    夢中&#x1f60d;サーバーレス(クラウド)、ブロックチェーン、機械学習
                </Typography>
                </div>
                <Slide direction={'down'} in={true}>
                    <div className={classes.social}>
                        <IconButton onClick={this.opener('https://twitter.com/aknow21')}>
                            <img src={Twitter} className={classes.icon}/>
                        </IconButton>
                        <IconButton onClick={this.opener('https://www.facebook.com/tomoaki.kanayama.3')}>
                            <img src={Fb} className={classes.icon}/>
                        </IconButton>
                    </div>
                </Slide>
            </div>);
   }

   private opener = (link: string) => {
    return () => {
            window.open(link);
        };
   }
}

const StyledContainer = withStyles<TopProps & ClassNames>(styles)(Top);

export default StyledContainer;