import * as React from 'react';
import { StyleRules, WithStyles, withStyles } from '@material-ui/core/styles';
import { Typography, ButtonBase, Button, CardMedia } from '@material-ui/core';
import WebIcon from "@material-ui/icons/Web";
const PlayStoreImg = require("../images/playstore.png");
const AppStoreImg = require("../images/appstore.svg");
const githubImg = require("../images/github.png")

const styles: StyleRules<'titleBarText'|'header'|'titleBar'|'root'|'media'|'chip'|'chips'|'githubBadge'|'playStoreBadge'|'storeLinks'|'title'> = {
  header: {
    position: 'relative',
    width: '100%'
  },
  titleBarText: {
    color: '#EEEEEE'
  },
  titleBar: {
    position: 'absolute',
    bottom: 0,
    width: '100%',
    backgroundColor: 'rgba(0,0,0, 0.6)',
  },
  root: {
    marginTop: 10,
    marginLeft: 15,
    minWidth: 300,
    minHeight: 150,
    maxHeight: 230,
    display: 'block',
    backgroundColor: '#424242'
  },
  title:{
    width:"100%",
    boxSizing: 'border-box',
    marginLeft: '1%',
    display:"flex",
    fontSize: 24,
    justifyContent: "center",
    color: '#FFF',
    fontWeight: 700,
    height: 0,
    paddingBottom: '56.25%', 
  },
  media: {
    height: 0,
    paddingTop: '56.25%', // 16:9
  },
  chip: {
    margin: 2
  },
  chips: {
    display: 'flex',
    flexWrap: 'wrap'
  },
  playStoreBadge: {
    width: 142,
    height: 40
  },
  githubBadge: {
    height: 41,
    width: 100  
  },
  storeLinks: {
    marginTop: 5
  }
};

type ClassNames = keyof typeof styles;

export interface IContents {
    img?: string,
    title: string,
    description: string,
    techs: Tech[],
    link: {
        web?: string
        android?: string
        ios?: string
        github?: string
    }
}
export interface Tech {
    name: string
}

export interface CardPrpos  {
    constants: IContents;
}


class ContentCard extends React.Component<
                                            CardPrpos &
                                            WithStyles<ClassNames>, 
                                            {}> {

   public render(): JSX.Element {
        const {classes, constants} = this.props;
        const link = constants.link;

        return (
        <div className={classes.root}>
            <div className={classes.header}>
                {
                    constants.img ? 
                    <CardMedia
                        className={classes.media}
                        image={constants.img}
                        title={constants.title}
                    /> :
                    <Typography className={classes.title} style={{color: '#C0C0C0'}}>
                        <p>{constants.title}</p>
                    </Typography>
                }
                <div className={classes.titleBar}>
                    <Typography className={classes.titleBarText} variant="title">{constants.title}</Typography>
                    <Typography className={classes.titleBarText} variant="subtitle1">{constants.description}</Typography>
                </div>
            </div>
                   <div className= {classes.storeLinks} >
                        { link.android &&
                            <ButtonBase onClick= {this.linkToPlayStore}>
                                <img src={PlayStoreImg} className={classes.playStoreBadge} />
                            </ButtonBase>
                        } 
                        { link.ios && 
                            <ButtonBase onClick= {this.linkToAppStore}>
                                <img src={AppStoreImg} />
                            </ButtonBase>
                        }
                        { link.web && 
                            <Button variant="outlined" color="inherit" onClick= {this.linkToWebPage} >
                            <WebIcon style={{color: '#C0C0C0'}} />
                            <Typography variant="body2" color="inherit" style={{color: '#C0C0C0'}}>
                            Web
                            </Typography>
                            </Button>
                        }
                        { link.github && 
                            <ButtonBase  onClick={this.linkToGithub} >
                                <img src={githubImg} className={classes.githubBadge} />                           
                            </ButtonBase>
                        }
                    </div>
        </div>
        );
    }

    private linkToPlayStore = () => {
            window.open(this.props.constants.link.android);
    }
    private linkToAppStore = () => {
            window.open(this.props.constants.link.ios);
    }
    private linkToWebPage = () => {
            window.open(this.props.constants.link.web);
    }

    private linkToGithub = () => {
            window.open(this.props.constants.link.github);
    }
}

const StyledContainer = withStyles< CardPrpos &{} & ClassNames>(styles)(ContentCard);

export default StyledContainer;