import React from 'react'
import { StyleRules, WithStyles, withStyles } from '@material-ui/core/styles';
import { Button, Typography } from '@material-ui/core';
import ArrowDown from '@material-ui/icons/KeyboardArrowDownTwoTone'
import ArrowUP from '@material-ui/icons/KeyboardArrowUpTwoTone'

const styles: StyleRules<'next'|'before'|'linkContent'> = {
  linkContent: {
    top: 0,
    position: 'absolute',
    display: 'flex',
    justifyContent: 'center',
    width: '100%',
  },
  next: {
    display: 'flex',
    flexDirection: 'column',
    position: 'fixed',
    bottom: 0,
  },
  before: {
    display: 'flex',
    flexDirection: 'column',
    position: 'fixed',
    top: 0,
  }
} 
type ClassNames = keyof typeof styles;

interface LinkProps {
  nextLabel?: string;
  beforeLabel?: string;
  next?: () => void;
  before?: () => void;
}

const LinkContent: React.FunctionComponent<WithStyles<ClassNames> & LinkProps> = (props) => {
  const { classes, next, before, nextLabel, beforeLabel } = props;
  return <div className={classes.linkContent}>
    { before && beforeLabel &&
        <Button className={classes.before} onClick={before}>
            <Typography style={{color: '#FFFFFF'}}>{ beforeLabel }</Typography>
            <ArrowUP fontSize={'large'} style={{color: '#FFFFFF'}}/>
        </Button>
    }
    { next && nextLabel &&
        <Button className={classes.next} onClick={next}>
            <Typography style={{color: '#FFFFFF'}}>{ nextLabel }</Typography>
            <ArrowDown fontSize={'large'} style={{color: '#FFFFFF'}}/>
        </Button>
    }
  </div>
}


export default withStyles<ClassNames>(styles)(LinkContent)